body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header
{
  background-color: black;
  color:white;
  padding:20px 70px;;
  display:flex;
  justify-content: space-between;
}
.title{
  font-size: 30px;
}
.cart-icon .counter
{
       height: 15px;
       width:15px;
       background-color: blue;
       border-radius: 50%;
       position:relative;
       right:7px;
       top:-3px;
       display:flex;
       align-items: center;
       justify-content: center;
       font-size:15px;
}
.cart-icon{
  display:flex;
}
.icon{
  font-size: 22px;
}img{
  height:150px;
}
.cart-wrapper{
  display:inline-flex;
  width:320px;
  box-shadow:0px 0px 7px rgb(116, 113, 113);
}
.image-wrapper{
  width:180px;
  display:flex;
  align-items: center;
  justify-content: center;
  padding:10px;
  border:1px solid grey;
  border-radius: 5px;
}
.content{
  flex-grow:enabled;
  gap:10px;
  display:flex;
  flex-direction: column;
  padding:10px;
}
.price{
  margin-bottom:auto;
}
.content button{
  background-color: black;
  color:white;
  border-radius: 3px;
}
.quantity{
  display:flex;
  align-self:flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.quantity div{
  height:22px;
  width:22px;
  display:flex;
  align-items:center;
  justify-content: center;
}
.quantity > div:first-child,
.quantity > button{
  background-color: blue;
  color:white;
  border-radius: 0;
  margin:0;
}
.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.footer{
  box-sizing: border-box;
  background-color: grey;
  display:flex;
  align-items:flex-end;
  justify-content: flex-end;
  padding:17px 75px;
  bottom: 0;
  font-size: 30px;;
}